import { default as Dayjs } from "dayjs";
import { default as localizedFormat } from "dayjs/plugin/localizedFormat.js";
import { default as objectSupport } from "dayjs/plugin/objectSupport.js";
import { default as timezone } from "dayjs/plugin/timezone.js";
import { default as utc } from "dayjs/plugin/utc.js";

Dayjs.extend(localizedFormat);
Dayjs.extend(objectSupport);
Dayjs.extend(utc);
Dayjs.extend(timezone);

export interface Locale {
  name: string;
  weekdays?: string[];
  months?: string[];
  weekStart?: number;
  weekdaysShort?: string[];
  monthsShort?: string[];
  weekdaysMin?: string[];
  yearStart?: number;
  ordinal?: (number: number, period?: string) => number | string;
  meridiem?: (hour: number, minute: number) => string;
  formats: Partial<{
    LT: string;
    LTS: string;
    L: string;
    LL: string;
    LLL: string;
    LLLL: string;
    l: string;
    ll: string;
    lll: string;
    llll: string;
  }>;
  relativeTime: Partial<{
    future: string;
    past: string;
    s: string;
    m: string;
    mm: string;
    h: string;
    hh: string;
    d: string;
    dd: string;
    M: string;
    MM: string;
    y: string;
    yy: string;
  }>;
}

